import React from "react";
import { useFilter } from "./NewItemTableFilters";
import { Table, TableBody, TableCell, TableContainer, TableHead, TableRow } from "@material-ui/core";
import Styles from "../ItemTable/ItemTable.module.scss";
import { usePagination } from "./NewItemTablePagination";
import { NewItemTableRow } from "./NewItemTableRow";
export function NewItemTable({ extraData, columns, items, filter, expander }) {
    let { filtered, FilterComponent } = useFilter({ items, filter });
    let { sliced, PaginationComponent } = usePagination({ items: filtered });
    return <div>
        <div style={{ all: 'initial' }}>
            {FilterComponent}
            <TableContainer className={Styles.itemTable}>
                <Table size="small">
                    <TableHead>
                        <TableRow>
                            {expander && <TableCell />}
                            {columns.map((c, i) => <TableCell key={i}>{c.label}</TableCell>)}
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {sliced.map((item, index) => <NewItemTableRow key={index} item={item} columns={columns} extraData={extraData} expander={expander}/>)}
                    </TableBody>
                </Table>
            </TableContainer>
            <div style={{ display: "flex" }}>
                {PaginationComponent}
            </div>
        </div>
    </div>;
}
